import React from 'react';
import { Link } from 'react-router-dom';
import '../Manager/manageOrder.css'
import { useState } from 'react';
const ManageOrder = () => {
    
    let IncomingOrders = JSON.parse(localStorage.getItem('orderStock'))
    const [orderCard, setorderCard] = useState(IncomingOrders)
   
       const SearchHandler = () =>{
        let seachTeam = JSON.parse(localStorage.getItem('orderStock')) 
        var inputVal = document.getElementById("searchField").value;
             if (inputVal == "") setorderCard(IncomingOrders) 
             else {
                var filterData = seachTeam.filter(x =>
                  x.name.toLowerCase().includes(inputVal.toLowerCase()) || 
                   x.mobile.toLowerCase().includes(inputVal.toLowerCase()) ||
                   (x.id).toString().includes(inputVal)
                  )
                  setorderCard(filterData)
        }
        console.log(filterData)
       }
    return (
        <div id='maindiv'>
            <nav id='manageordnav'>
                <div>
                    <h1 id="welcome">Welcome</h1>
                    <h3 id='name'>Aministrator</h3>
                </div>
                <div id='search'>
                    <input type={Text} id='searchField' Placeholder="Search Order" onKeyUp={()=>{SearchHandler()}}></input><span><i class="fas fa-search"></i></span>
                </div>
                <div id="desdiv">
                    <h3 id="des">Store Manager</h3>
                    <Link to = '/storemanager'  style={{textDecoration:"none"}}> <i id='homecoming'  class="fas fa-user-tie"></i></Link>
                </div>
            </nav>
            <div id='content'>
                <h1>Manage Orders</h1>
                <Link to = '/neworder' ><button class='buttonClass3'>NEW ORDER</button></Link>
                <div id='ordercontainer'>
                {orderCard.map(item => <OrderSet {...item}></OrderSet>)}
               
                    
                </div>
            </div>
        </div>
    );
}

export default ManageOrder;
const OrderSet = ({id,name,mobile,products,total,createdby}) => {
    let linky = '/CRUDorder' +'?'+ 'id=' +id
    return ( 
      <Link to={linky} style={{textDecoration:"none",width:"40%"}}>  
      <div id='orderDetail'>
        <h3>Order No -<span>{id}</span></h3>
        <h3>Customer Name - <span>{name}</span></h3>
        <h3>Mob: {mobile}</h3>
        <div id="medHold">
        {products.map(item => <ProSet {...item}></ProSet>)}</div>
        <h3>Rs. {total}</h3>
    </div> </Link>
     );
}
const ProSet = ({mediName,quantity}) => {
    return ( 
        <div id="product">
         <span>Name: {mediName}</span><span style={{marginLeft:"3%"}}>Qty: {quantity}</span>
        </div>
     );
}
 