import React from 'react';
import { HashRouter, Route, Switch} from 'react-router-dom';
import StoreManager from './Components/Manager';
import SalesHome from './Components/Executive';
import InvManage from './Components/Manager/manageinventory';
import EditTeam from './Components/Manager/EditTeam';
import ManageOrder from './Components/Manager/manageOrder';
import CRUDmed from './Components/Manager/CRUDmed';
import CRUDteam from './Components/Manager/teammember';
import CRUDorder from './Components/Manager/CRUDorder';
import NewOrder from './Components/Manager/NewOrder';
import ExeCrud from './Components/Executive/ChangeOrder';
import NewOrderExe from './Components/Executive/ChangeOrder';
import Logins from './Components';

const PharmaManagemnet = () => {
  return ( 
    <HashRouter>
      <div>
    <Switch>
        <Route path="/storemanager" component={StoreManager}></Route>
        <Route path ='/sales' component={SalesHome}/>
        <Route path ='/medInv' component={InvManage}/>
        <Route path='/myteam' component={EditTeam}/>
        <Route path='/order' component={ManageOrder}/>
        <Route path='/CRUDmed' component={CRUDmed}/>
        <Route path='/CRUDteam' component={CRUDteam}/>
        <Route path='/CRUDorder' component={CRUDorder}/>
        <Route path='/NewOrder' component={NewOrder}/>
        <Route path='/exeordup' component={ExeCrud}/>
        <Route path='/exeordupexe' component={NewOrderExe}/>
        <Route path='/login' component={Logins}/>
        <Logins></Logins>
        </Switch>
  </div>
  
  
        </HashRouter>);
}
 
export default PharmaManagemnet;