import React from 'react';
import { Link } from 'react-router-dom';
import '../Manager/index.css'
const StoreManager = () => {
    let Medicines = [
        {
            id: 1,
            Name: "Paracetamol",
            MafName: "Cipla",
            Price:15,
            Stock: 100,
            Discount: 20
        },
        {
            id: 2,
            Name: "Saradon",
            MafName: "Accure",
            Price: 50,
            Stock: 200,
            Discount: 10
        },
        {
            id: 3,
            Name: "Vicks 500",
            MafName: "Vicks",
            Price: 80,
            Stock: 500,
            Discount:50
        },
        {
            id: 4,
            Name: "Disprin",
            MafName: "Azure",
            Price: 20,
            Stock: 100,
            Discount: 0
        },
        {
            id: 5,
            Name: "Ibuprofen",
            MafName: "Dr. Reddy",
            Price: 50,
            Stock: 10,
            Discount: 30
        },
    ]
    let team = [
        {
            id: 1,
            First_name: "Shubham",
            Second_name: 'Upadhyay',
            Avatar: "fas fa-user-tie",
            DOB: "26/07/1995",
            Gender: "Male",
            Exp: 5,
            Password: "abc",
            img: "https://www.sasscaffold.com.au/wp-content/uploads/2019/10/pngtree-business-male-user-avatar-vector-png-image_1511454.jpg"
        },
        {
            id: 2,
            First_name: "Shubham",
            Second_name: 'Upadhyay',
            Avatar: "fas fa-user-tie",
            DOB: "26/07/1995",
            Gender: "Male",
            Exp: 5,
            Password: "abc",
            img: "https://www.sasscaffold.com.au/wp-content/uploads/2019/10/pngtree-business-male-user-avatar-vector-png-image_1511454.jpg"

        },
        {
            id: 3,
            First_name: "Shubham",
            Second_name: 'Upadhyay',
            Avatar: "fas fa-user-tie",
            DOB: "26/07/1995",
            Gender: "Male",
            Exp: 5,
            Password: "abc",
            img: "https://www.sasscaffold.com.au/wp-content/uploads/2019/10/pngtree-business-male-user-avatar-vector-png-image_1511454.jpg"
        },
        {
            id: 4,
            First_name: "Shubham",
            Second_name: 'Upadhyay',
            Avatar: "fas fa-user-tie",
            DOB: "26/07/1995",
            Gender: "Male",
            Exp: 5,
            Password: "abc",
            img: "https://www.sasscaffold.com.au/wp-content/uploads/2019/10/pngtree-business-male-user-avatar-vector-png-image_1511454.jpg"
        },

    ]
    let orders = [
    ];
    let IncomingMedicine = [];
    let IncomingTeam = [];
    let IncomingOrders = [];
    const checkLocal = () => {
        if (localStorage.getItem("MedicineStock"))
            IncomingMedicine = JSON.parse(localStorage.getItem('MedicineStock'))
        else {
            IncomingMedicine = Medicines;
            localStorage.setItem("MedicineStock", JSON.stringify(IncomingMedicine))
        }
        if (localStorage.getItem("TeamStock"))
            IncomingTeam = JSON.parse(localStorage.getItem('TeamStock'))
        else {
            IncomingTeam = team;
            localStorage.setItem("TeamStock", JSON.stringify(IncomingTeam))
        }
        if (localStorage.getItem("orderStock"))
            IncomingOrders = JSON.parse(localStorage.getItem('orderStock'))
        else {
            IncomingOrders = orders;
            localStorage.setItem("orderStock", JSON.stringify(IncomingOrders))
        }
    }
    checkLocal();
    const arraylimiter = (a, b) => {
        let newarray = []
        for (let i = 0; i < b; i++) newarray.push(a.pop())
        return newarray
    }
    return (
        <div id="maindiv">
            <nav id='navbaring'>
                <div>
                    <h1 id="welcome">Welcome</h1>
                  <Link to = '/login'>  <button  id='logouting'>Logout</button> </Link>
                    <h3 id='name'>Aministrator </h3>
                </div>
                <div id="desdiv">
                    <h3 id="des">Store Manager</h3>
                    <Link to = '/storemanager' style={{ textDecoration: 'none' }}> <i id ='homecoming' class="fas fa-user-tie"></i></Link>
                </div>
            </nav>
            <div id='contentDiv'>
                <div id="manageInventory">
                    <h1>Manage Inventory</h1>
                    <div id="inventory">
                        {arraylimiter(IncomingMedicine, 4).map(item => <Medset {...item}></Medset>)}
                    </div>
                    <Link to='/medInv'> <button id='manageInvButton' class='buttonClass'>Manage More</button></Link>
                </div>
                <div id="ManageTeam">
                    <h1>Manage Team</h1>
                    <div id="Team">
                        {arraylimiter(IncomingTeam, 2).map(item => <Teamset {...item}></Teamset>)}
                    </div>
                    <Link to='/myteam'>  <button id='manageTeamButton' class='buttonClass'>Manage More</button></Link>

                </div>
                <div id="manageOrders">
                    <h1>Manage Orders</h1>
                    <div id="orders">
                        {arraylimiter(IncomingOrders, 1).map(item => <OrderSet {...item}></OrderSet>)}

                    </div>
                    <Link to='/order'> <button id='manageOrderButton' class='buttonClass'>Manage More</button> </Link>
                </div>
            </div>
        </div>

    );
}

export default StoreManager;

const Medset = ({ Name, MafName, Stock }) => {
    return (
        <div id='meddetails'>
            <h3>{Name}</h3>
            <h3>{MafName}</h3>
            <h4>In Stock<span class="ml">{Stock} Units</span></h4>
        </div>
    );
}

const Teamset = ({ First_name, Second_name, Avatar }) => {
    return (
        <div id='TeamDetails'>
            <i class={Avatar}></i>
            <p><span>{First_name}</span><span style={{marginLeft:"5px"}}>{Second_name}</span></p>
        </div>
    );
}
const OrderSet = ({ name, id, createdby, total }) => {
    return (
        <div id='orderdetails'>
            <h3>Name: {name}</h3>
            <h4>Order No.<span>{id}</span></h4>
            <h4>Total <span>Rs. {total}</span></h4>
        </div>
    );
}