import React from 'react';
import { Link } from 'react-router-dom';
import '../Executive/exeindex.css'
import { useLocation } from 'react-router';
import { useState } from 'react';
const SalesHome = () => {
    let id = useLocation().search
    const idp = new URLSearchParams(id).get('id')
    console.log(idp)
    let id1 = useLocation().search
    const idp1 = new URLSearchParams(id1).get('name')
    console.log("idp1",idp1)
    let use;
    const settingName = () =>{
        if(idp)
        use =idp
        else
        use=idp1
    }
    settingName()
    let linky = '/exeordupexe'  +'?'+ 'id=' +idp;
    const filterArr=()=>{
        let product = JSON.parse(localStorage.getItem('orderStock'))
        let r=[];
        if(product)
        for(let i=0;i<product.length;i++) product[i].createdby==use && r.push(product[i])
        return r;
    }
    let product=filterArr();
    const [orderCard, setorderCard] = useState(product)
    const SearchHandler = () =>{
        let seachTeam = JSON.parse(localStorage.getItem('orderStock')) 
        var inputVal = document.getElementById("searchField").value;
             if (inputVal == "") setorderCard(product) 
             else {
                var filterData = seachTeam.filter(x =>
                  x.name.toLowerCase().includes(inputVal.toLowerCase()) || 
                   x.mobile.toLowerCase().includes(inputVal.toLowerCase()) ||
                   (x.id).toString().includes(inputVal)
                  )
                  setorderCard(filterData)
        }
        console.log(filterData)
       }
    return ( 
        <div id='maindiv'>
           <nav>
                <div>
                    <h1 id="welcome">Welcome</h1>
                  <Link to = '/login'>  <button  id='logout'>Logout</button> </Link>
                    <h3 id='name'>{use}</h3>
                </div>
                <div id='search'>
                    <input type={Text} id='searchField' Placeholder="Search medicine" onKeyUp={() => { SearchHandler() }}></input><span><i class="fas fa-search"></i></span>
                </div>
                <div id="desdiv">
                    <h3 id="des">Sales Executive</h3><i id ='homecoming' class="fas fa-user-tie"></i>
                </div>
            </nav>
            <div id='content'>
                <h1>Orders</h1>
                <Link to = {linky}><button class='buttonClass3'>NEW ORDER</button></Link>
                <div id='ordercontainer'>
                {orderCard.map(item => <OrderSet {...item}></OrderSet>)}
                </div>
                </div>
        </div>
     );
}
 
export default SalesHome;
const OrderSet = ({id,name,mobile,products,total,createdby}) => {
    return (   <div id='orderDetailing'>
        <h3>Order No -<span>{id}</span></h3>
        <h3>Customer Name - <span>{name}</span></h3>
        <h3>Mob: {mobile}</h3>
        <div id="medHold"> {products.map(item => <ProSet {...item}></ProSet>)}</div>
        <h3>Total-<span>Rs. {total}</span></h3>
    </div> 
     );
}
const ProSet = ({mediName,quantity}) => {
    return ( 
        <h3 id="product">
         <span>{mediName}</span><span>{quantity}</span>
        </h3>
     );
}
 