import React from 'react';
import { useLocation, useParams } from 'react-router';
import { Link } from 'react-router-dom';
import '../Manager/CRUDmed.css'
const CRUDmed = (props) => {
    let id = useLocation().search
    const idp = new URLSearchParams(id).get('id')
    console.log(idp)
    const incoData = JSON.parse(localStorage.getItem('MedicineStock'))
    let temp;
    const data=()=>{
        for(let i=0;i<incoData.length;i++){
            if(incoData[i].id==idp){
                temp=incoData[i];
                break;
            }
        }
    }
    console.log(temp)
    const handleDelete = ()=>{
        let j ;
        for(let i=0;i<incoData.length;i++){
            if(incoData[i].id==idp){
                j=i;
                break;
            }
        }
        localStorage.setItem(
            "MedicineStock",
            JSON.stringify(
              incoData.slice(0, j).concat(incoData.slice(j + 1))
            )
          );
    }
    const handleUpdate = ()=>{
        let tempUpdate;
        let i;
        for(i=0;i<incoData.length;i++){
            if(incoData[i].id==idp){
                tempUpdate =incoData[i];
                break;
            }
        }
       var obj1 =  {
            id:tempUpdate.id,
            Name:document.getElementById("medName1").innerText,
            MafName:document.getElementById("mafName1").innerText,
            Price:document.getElementById("price1").innerText,
            Discount:document.getElementById("discount1").innerText,
            Stock:document.getElementById("stocks1").innerText
        }
        incoData[i]=obj1
        localStorage.setItem("MedicineStock",JSON.stringify(incoData))
    }

   
    return ( 
        <div id='maindiv'>
            {data()}
            <nav id='crudmednav'>
                <div>
                    <h1 id="welcome">Welcome</h1>
                    <h3 id='name'>Shubham Upadhyay</h3>
                </div>
                <div id="desdiv">
                    <h3 id="des">Store Manager</h3>
                   <Link to = '/storemanager'  style={{textDecoration:"none"}}> <i id='homecoming' class="fas fa-user-tie"></i></Link>
                </div>
            </nav>
            <div id='formdiving'>
                <h1>Manage Medicine</h1>
                <form>
               <div class='setwidth'> <span contentEditable='false' class='editing'>Name:<span id='medName1' className='medName1' contentEditable='true'> {temp.Name}</span></span>  </div>
               <div class='setwidth'>  <span contentEditable='false' class='editing'>Maf Name:  <span id='mafName1' className='mafName1' contentEditable>{temp.MafName} </span></span></div>
               <div class='setwidth'>  <span contentEditable='false' class='editing'>In Stock:<span id='stocks1' className='stock1'  contentEditable='true'>{temp.Stock}</span></span></div>
               <div class='setwidth'>  <span contentEditable='false' class='editing'>Price: Rs<span id='price1' className='price1'  contentEditable>{temp.Price}</span></span></div>
               <div class='setwidth'>  <span contentEditable='false' class='editing'>Discount: <span id='discount1' className='discount1' contentEditable>{temp.Discount}</span><span>%</span></span></div>
                  
                </form>
                <div id='buttonDiv'>
                <Link to='/medInv' onClick={()=>handleUpdate()}><button class='buttonClass4' id='update'>Update</button></Link>
                <Link to='/medInv' onClick={()=>handleDelete()}><button class='buttonClass4' id='delete'>Delete</button></Link>
                </div>
            </div>
        </div>
     );
}
 
export default CRUDmed;
